%bg {
    background-repeat: no-repeat; 
    background-size: cover;
}

%btnhov {
    button:hover {
        border: 1px solid rgba(188, 188, 188, 0.445);
    }
}

.navigation {
    @extend %bg;
    padding: 40px 4vw;
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    grid-template-rows: min-content;
    position: relative;
    z-index: 1;

    .logotop {
        width: 12vw;
        min-width: 150px;
    }

    .navlinks {
        display: flex;
        gap: 20px;
        justify-content: right; 

        .Link {
            color: white;
            text-decoration: none;
            font-size: 17px;
            padding: 5px 0px;
            display: flex;
            justify-content: center;
            align-items: center;

            button {
                border: 1px solid white;
                background-color: rgba(0, 0, 0, 0);
                padding: 10px 30px;
                border-radius: 50px;
                color: white;
                font-weight: 600;
                cursor: pointer;
            }

            @extend %btnhov;

            .web {
                width: 25px;
            }
        }
    }

    
}